@charset "UTF-8";

header {
    width: 100%;
    height: 80px;
    background: var(--main_color);
}

.header-inner {
    width: 1280px;
    height: 100%;
    margin: auto;
    display: flex;
    justify-content: space-between;
}

h1 {
    width: 160px;
    height: 80px;
    background: gold;
    font-size: 26px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.right-header{
    display: flex;
}

.main-menu {
    display: flex;
    height: 100%;
    width: auto;
}

.main-menu>li {
    width: auto;
    height: 100%;
    font-size: 20px;
    position: relative;
}

.main-menu>li>a {
    padding: 0 36px;
    height: 100%;
    line-height: 80px;
    color: var(--prime_text_color);
}

.sub-menu {
    width: 100%;
    height: auto;
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    background: var(--prime_text_color);
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    z-index: 100;
}

.sub-menu>li>a {
    padding: 10px 16px;
    color: var(--main_color);
    white-space: nowrap;
}

.main-menu>li:hover>a{
    background: var(--prime_text_color);
    color: var(--main_color);
}

.main-menu>li:hover>.sub-menu {
    display: block;
}

.main-menu>li:hover>.sub-menu>li>a{
    background: var(--main_color);
    color: var(--prime_text_color);
}

.main-menu>li>.sub-menu>li:hover>a{
    background: var(--prime_text_color);
    color: var(--main_color);
}
.m_btn{
    width: 30px;
    height: 25px;
    position: relative;
    cursor: pointer;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
}
.m_btn span{
    position: absolute;
    width: 100%;
    height: 2px;
    background: #fff;
    left: 0;
}
.m_btn span:nth-child(1){
    top: 0;
    transition: all 0.5s ease-in-out;
}
.m_btn span:nth-child(2){
    top: 10px;
    transition: all 0.5s ease-in-out;
}
.m_btn span:nth-child(3){
    top: 20px;
    transition: all 0.5s ease-in-out;
}
.on .m_btn span:nth-child(1){
    transform: rotate(45deg);
    top: 10px;
}
.on .m_btn span:nth-child(2){
    transform: translateX(100%);
    opacity: 0;
}
.on .m_btn span:nth-child(3){
    transform: rotate(-45deg);
    top: 10px;
}